.transactions {
  display: block;
  margin-top: 20px;
  margin-bottom: 20px;
}

.transactionHistory {
  table-layout: fixed;
  /* width: 100%; */
  /* min-width: 400px; */
  
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  
  border-collapse: collapse;
  white-space: nowrap;

  color: black;
  box-shadow: 1px 5px 5px 0px rgba(0, 0, 0, 0.3);
}

.transactionHistory thead {
  color: white;
  text-shadow: 0 1px 1px #2D2020;
  background-color: rgb(129, 127, 255);
  padding: 10px;
}

.transactionHistory th {
  min-width: 100px;
  padding: 8px;

  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.transactionHistory tbody td {
  padding: 8px;
  
  border-bottom: 1px solid darkgray;
  border-right: 1px solid darkgray;

  transition: border-bottom 150ms linear, 
              font-weight 150ms linear;
}

.transactionHistory tbody td:last-child {
  border-right: none;
}

.transactionHistory tbody tr:nth-child(odd) {
  background-color: white;
}

.transactionHistory tbody tr:nth-child(even) {
  background-color: lightgrey;
}

.transactionHistory tbody tr:hover td {
  border-bottom: 1px solid black;
  font-weight: 700;
}